<template>
    <section class="chart-container">
        <el-row>
            <el-col :span="16">
                <div id="chartBar" style="width:100%; height:400px;"></div>
            </el-col>
        </el-row>
    </section>
</template>

<script>
    import echarts from 'echarts'

    export default {
        data() {
            return {
                chartBar: null
            }
        },

        methods: {
            drawBarChart() {
                this.chartBar = echarts.init(document.getElementById('chartBar'));
                this.chartBar.setOption({
                    title: {
                        text: '黄桃销售数据',
                        subtext: '城市历年累计销售额 >5 进行统计'
                    },
                    tooltip: {
                        trigger: 'axis',
                        axisPointer: {
                            type: 'shadow'
                        }
                    },
                    legend: {
                        data: ['2016年','2017年', '2018年']
                    },
                    grid: {
                        left: '3%',
                        right: '4%',
                        bottom: '3%',
                        containLabel: true
                    },
                    xAxis: {
                        type: 'value',
                        boundaryGap: [0, 0.01]
                    },
                    yAxis: {
                        type: 'category',
                        data: [ '杭州(箱)', '温州(箱)']
                    },
                    series: [
                        {
                            name: '2016年',
                            type: 'bar',
                            data: [42, 39]
                        },
                        {
                            name: '2017年',
                            type: 'bar',
                            data: [36, 61]
                        },
                        {
                            name: '2018年',
                            type: 'bar',
                            data: [70, 89]
                        }
                    ]
                });
            },
            drawCharts() {
                this.drawBarChart()
            },
        },
        mounted: function () {
            this.drawCharts()
        },
        updated: function () {
            this.drawCharts()
        }
    }
</script>

<style scoped>
    .chart-container {
        width: 100%;
        float: left;
    }
    /*.chart div {
        height: 400px;
        float: left;
    }*/

    .el-col {
        padding: 30px 20px;
    }
</style>



